<script>
import DataService from "../../../service/DataService"
import WidgetReport from './WidgetReport.vue';
import DateRange from './DateRange.vue';
export default {
  components: { WidgetReport, DateRange},
    data() {
   return {
    titleName:'',
    moduleName:'Asset',
    widgets:[],
    viewType:'R',
    fromDate:null,
    toDate:null,
    MenuButton:false,
    toDos:[],
    encCompanyId:null,
    encUserId:null,
    encTrackId:null
   }
},
created:function(){
    this.GetReports();
},
mounted(){
            $("#divReportsdata, #divAddMoreReports").show();
            $("#divReportsdata").addClass("col-md-12").removeClass("col-md-7").show();
            $("#divReportsdata").find(".graph-widget").addClass("col-md-4").removeClass("col-md-6");   
},
methods:{
    OnAddMore(){
   this.$refs.widget.$data.showReports=true;
    },
    FetchCountListData(type){
        this.$refs.countList.$data.FetchData(type,this.$refs.countList.$data.users);
    },
    showDropDowMenu(){
        debugger
        this.MenuButton=!this.MenuButton;
    },
    GetToDoList(){
        var vm = this;
        DataService.gettoDoList('ModuleName=' + vm.moduleName)
            .then(function (response) {
                console.log(response);
                vm.encCompanyId=response.data.COMPANY_ID;
                vm.encUserId=response.data.CREATED_BY;
                vm.encTrackId=response.data.TRACK_ID;
                //vm.toDos = response.data.list;
            })
    },
    GetReports(){
        var vm = this;
        DataService.GetReports('moduleName=' + vm.moduleName)
            .then(function (response) {               
             vm.widgets=response.data.list;
             //vm.GetToDoList();
            })
    },
    SetDateRange(from, to) {       
        this.fromDate = from;
        this.toDate = to;
        this.GetReports();
    },
   
}
}
</script>