<template>
    <div class="w-100">
        <!--LIST TOGGLE FULL SLIDE BUTTON-->
        <div id="divshowTicketList" class="w-100 float-left">
            <div id="divReports">
                <reports v-if="showReports == true" :moduleName="moduleName" @close="OnCloseReport"></reports>
            </div>
        </div>
        <div class="tab-pane fade show active border-0 p-0" id="dashboard" role="tabpanel" aria-labelledby="dashboard-tab">
            <!--dashboard-->
            <!--dashboard-->
            <div class="clearfix"></div>
            <div class="dashbord-graph-widget row ml-0 mr-0 padding-t_10 usercustomwidth" id="divInboxSibling">
                <div v-for="list in widgets" :key="list"
                    :class="{ 'graph-widget align-self-stretch ticketingDashboardHead reportcontainer mb-2': true, 'col-md-6': viewType == 'R' }"
                    :data-id="list.reporT_ID" :daction="list.action" :dctrl="list.controller">
                    <div class="dashboard-boxes graph-widget-box bg-white" v-if="list.isselected">
                        <div class="graph-widget-heading float-left w-100">
                            <span class="text-truncate float-left w-75">{{ $t(list.reporT_HEADING) }}</span>
                            <date-range :pgaction="list.action" @set-date="SetDateRange"></date-range>   
                            <div class="mw-50 float-right">
                          
                           
                                <a :reportid="list.reporT_ID" href="javascript:;" class="hlkDeleteReport float-right"
                                    title="Remove" @click="onRemoveReport($event, list.reporT_ID)">
                                    <span class="icon_close">
                                        <i class="fa fa-times"></i>
                                    </span>
                                </a>
                                <a href="javascript:;" :id="list.reporT_ID" v-on:click="zoominOut(list.reporT_ID)"  class="zoomin rptexpand ancdashboard float-right">
                                    <span class="icon_expand">
                                        <i class="fa fa-expand"></i>
                                    </span>
                                </a>
                            </div>
                        </div>
                        <div :action="list.action" :controller="list.controller"
                            :class="{ 'raph-widget-content bg-white float-left w-100': true, 'd-flex  align-content-center justify-content-center': (list.charT_TYPE == 'pie' || list.charT_TYPE == 'donut' || list.charT_TYPE == 'bar') }">
                            <PieChart v-if="list.widgeT_TYPE == 'widget' && list.charT_TYPE == 'pie'"
                                :moduleName="moduleName" :widgetInfo="list" :fromDate="fromDate"
                                :toDate="toDate"></PieChart>

                        </div>
                    </div>
                </div>

                <!-- end right panel content -->
            </div>
            <div class="clearfix"></div>
        </div>
        <!-- end right panel content -->
    </div>
</template>

<script>
import DataService from '../../../service/DataService';
import BarChart from './BarChart.vue';
import PieChart from './PieChart.vue';
import Reports from './Reports.vue';
import DateRange from './DateRange.vue';

export default {
    components: { BarChart, PieChart, Reports ,DateRange},
    props: {
        widgets: null,
        viewType: null,
        moduleName: null,
        fromDate: null,
        toDate: null
    },
    data() {
        return {
            isLoading: false,
            showReports: false
        }
    },
    created() {
    },
    methods: {
        OnCloseReport() {
            this.showReports = false;
            this.$emit('reload-report');
        },
        zoominOut(id){
         debugger
            var idval='#'+id;
            if ($(idval).hasClass('zoomin')) {
                $(idval).removeClass('zoomin')
                $(idval).addClass('zoomout')
                $(idval).find('i').removeClass('fa fa-expand')
                $(idval).find('i').addClass('fa fa-compress')
                $("[data-id="+id+"]").removeClass('col-md-6');
                $("[data-id="+id+"]").addClass('col-md-4 col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3');
            }
            else {
                $(idval).removeClass('zoomout')
                $(idval).addClass('zoomin')
                $(idval).find('i').removeClass('fa fa-compress')
                $(idval).find('i').addClass('fa fa-expand')

                $("[data-id="+id+"]").removeClass('col-md-4 col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3');
                $("[data-id="+id+"]").addClass('col-md-6');
            }
            
        },
        onRemoveReport(event, reportId) {
            var vm = this;
            event.stopImmediatePropagation();
            var totalReport = $('div.reportcontainer');
            if (totalReport.length <= 2) {
                vm.ShowAlert(vm.$t('AtleastTwoReport'), "warning", true, vm.$t("Alert"));
                return;
            }
            var msg = vm.$t('RemoveReportWarning');
            var alertTitle = vm.$t("Report");
            // var reportId = $(this).attr('reportid');
            var report = $(this).parents('div.reportcontainer');
            vm.confirmR(msg, true, alertTitle, function () {
                vm.removeReport(vm, reportId, report);
            });
        },
        removeReport(vm, reportId, report) {
            vm.isLoading = true;
            DataService.ReportsRemove('reportId=' + reportId)
                .then(function (response) {
                    debugger;
                    if (response.data) {
                        //  var paths = getRootURL();
                        var report = $(this).parents('div.reportcontainer');
                        $(report).remove();
                        $(report).show();
                        vm.$emit('reload-report');
                    } else {
                        vm.ShowAlert(vm.$t(InsertUpdateError), "failure", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }
                })
        },
        SaveReportPreferences(reportIds, moduleName) {
            var vm = this;
            showLoader();
            $.ajax({
                url: '@Url.Action("ReportsSave", "CompanyAdmin")',
                data: { reportIds: reportIds, moduleName: moduleName },
                cache: false,
                success: function (data, textStatus, xhr) {
                    if (xhr.status == "200") {
                        if (data == 1) {
                            var paths = getRootURL();
                            $('#divReports').dialog('close');
                            if ($("#hdnpagename").val() == "dashboard") {
                                loadReports();
                            }
                            else {
                                window.location.reload();
                            }
                        }
                        else if (data == 2) {
                            ShowAlert(vm.$t("DashboardReportsMaxLimit"), "failure", true, vm.$t("Alert"));
                        }
                        else {
                            ShowAlert(vm.$t("InsertUpdateError"), "failure", true, vm.$t("Alert"));
                        }
                    }
                    //hideLoader();
                },
                error: function (http, message, exc) {
                    hideLoader();
                }
            });
        },

    }
}
</script>
